
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default {
components: {
XHeader,
ViewBox,
Flexbox,
FlexboxItem,
Popup,
Loading
},
data() {
return {
open_forward: false,
personalCard: [],
show: false,
user_name: '',
user_info: ''
}
},
mounted() {
this.update();










console.log("传过来的user_id", this.$route.params.user_id);
},
methods: {
update() {
this.show = true;

this.$post(
"dynamic/getList", {},
res => {
this.personalCard = res.msg;
this.show = false;
if(res.res >= 1) {}
if(res < 0) {}
}
);
},

toGoodsInfo(item) {
var goods_id = item.goods_id
this.$router.push({
path: "/goodsInfo/goodsInfo",
params: {
goods_id: goods_id
}
});
},

toSocialInfo() {
this.$router.push({
path: "/socialInfo/socialInfo",
name: '/socialInfo/socialInfo',
params: {
openComment: "2",
}
});
},

thumbs(item) {
var dynamic_id = item.dynamic_id;

this.$post(
"dynamic/up", {
dynamic_id: dynamic_id
},
res => {
console.log(res);
if(res.res >= 1) {
item.is_thumbs_up == false;
this.$vux.toast.show({
text: "点赞成功",
type: "text",
position: "middle"
});
}
if(res.res < 0) {
this.$vux.toast.show({
text: "网络错误",
type: "text",
position: "middle"
});
}
}
);

},

cancel_thumbs(item) {
var dynamic_id = item.dynamic_id;
this.$post(
"dynamic/delUp", {
dynamic_id: dynamic_id
},
res => {
console.log(res);
if(res.res >= 1) {
item.is_thumbs_up == true;
this.$vux.toast.show({
text: "已取消点赞",
type: "text",
position: "middle"
});
}
if(res.res < 0) {
this.$vux.toast.show({
text: "网络错误",
type: "text",
position: "middle"
});
}
}
);
},

toComment() {
this.$router.push({
path: "/socialInfo/socialInfo",
name: '/socialInfo/socialInfo',
params: {
openComment: "1",
}
});
},
toGoodsInfo() {
this.$router.push({
path: "/goodsInfo/goodsInfo",

});
},

openForward() {
this.open_forward = true;
},

cancel() {
this.open_forward = false;
},

toGoodsInfo(item) {
var goods_id = item.goods_id
this.$router.push({
path: "/goodsInfo/goodsInfo",
name: '/goodsInfo/goodsInfo',
params: {
goods_id: goods_id,
}
});
},
}
}



